nav{
    background: rgba(0,0,0,0.3);
    width:max-content;
    padding:0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform:translate(-50%);
    bottom: 2rem;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
nav{
    background: transparent;
    padding: 0.9rem;
    border-radius:20%;
    display:flex;
    color: var(--color-light);
    font-size:1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.2)
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);

}